@import "../../../utils/styles/GymbyGeneralStyles.module.scss";
$cardHeight: 200px;
$cardWidth: 135px;

.addingCardPhotos {
  display: flex;
  &>*:not(:last-child) {
    margin-right: 20px;
  }
}

/*---------------------------*/
.photoCard {
  position: relative;
  height: $cardHeight;
  flex: 0 0 $cardWidth;
  overflow: hidden;
  display: flex;
  justify-content: flex-end;
  cursor: pointer;
  &:hover {
    filter: drop-shadow(0px 6px 9px rgba(0, 117, 255, 0.65));
  }

  & img {
    border-radius: 20px;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    object-fit: cover;
  }

 /* &:not(:hover) {
    .photoCard {
      filter: none;
    }
  }
*/

}


.basketIcon {
  cursor: pointer;
  width: 21px;
  height: 21px;
  position: relative;
  margin: 11px 11px 0 0;
  &:hover {
    filter: drop-shadow(0px 6px 9px $gymbyRed);
  }
  & img {
    border-radius: 5px;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }
}

/*----------------------------*/
.addingCard {
  height: $cardHeight;
  flex: 0 0 $cardWidth;
  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: inset 4px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  &:hover {
    box-shadow: inset 6px 6px 6px $gymbyLightBlue;
    border: 1px solid $gymbyLightBlue;
  }
  & img {}
}