.leftPanel__container {
  min-width: 400px;
  /*border-right: black 1px solid;*/
  padding: 30px 0 0 0;
  height: 100%;
  position: relative;
  &:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 1px;
    background-color: rgba(0, 0, 0, 0.15);
  }
}
.leftPanelList {
  display: flex;
  justify-content: center;
}